/* ==========================
Stili base (versione aggiornata)
========================== */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* { box-sizing: border-box; }

/* Links */
a {
  text-decoration: none;
  color: #007aff;
}
a:hover {
  color: #0055ff;
}

/* Headings & text */
h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
  color: #333;
  margin: 0 0 12px 0;
}
p {
  color: #666;
  margin: 0 0 12px 0;
}

/* Images */
img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  display: block;
}

/* Container */
.container {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

/* Header & nav */
header {
  background-color: #3498db;
  color: #fff;
  padding: 20px;
  text-align: center;
  border-radius: 10px 10px 0 0;
}
header h1 {
  font-size: 24px;
  margin: 0;
}

nav ul {
  list-style: none;
  margin: 12px 0 0 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  gap: 12px;
}
nav li { margin-right: 20px; }
nav a { color: #fff; text-decoration: none; }
nav a:hover { color: #ccc; }

/* Articoli layout */
.articoli {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
}
.articolo {
  margin: 0;
  padding: 20px;
  background-color: #f7f7f7;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: calc(50% - 10px);
  box-shadow: 0 2px 6px rgba(0,0,0,0.02);
}
.articolo h2 { margin-top: 0; font-size: 18px; }
.articolo p { margin-bottom: 0; }

/* ==========================
IA-CHAT: Base, responsive e mobile-first
========================== */
/* Mobile-first: regole base per piccoli schermi */
.ia-chat {
  position: fixed;
  bottom: 12px;
  right: 12px;
  left: 12px;                 /* rende la chat centrata e con margine su mobile */
  width: auto;
  max-width: none;
  padding: 12px;
  background-color: #f9f9f9;
  border: 1px solid #e6e6e6;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.08);
  z-index: 9999;
  transition: transform 240ms cubic-bezier(.2,.8,.2,1), opacity 180ms ease, visibility 180ms;
  transform-origin: center bottom;
  will-change: transform, opacity;
}

/* Stato nascosto (scorrimento verso il basso) */
.ia-chat.hidden {
  transform: translateY(120%);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

/* Contenuto interno chat */
.ia-chat h3 {
  margin: 0 0 8px 0;
  font-size: 16px;
}
.ia-chat .chat-window {
  max-height: 46vh;           /* usa viewport height per adattarsi meglio con keyboard */
  min-height: 80px;
  overflow-y: auto;
  margin-bottom: 8px;
  padding: 8px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #eee;
  -webkit-overflow-scrolling: touch;
}
.ia-chat .chat-window div { margin-bottom: 6px; }
.ia-chat .chat-window strong { display: inline-block; width: 40px; }

/* Input row: aumenta dimensione touch e rende i bottoni più leggibili */
.ia-chat .input-row {
  display: flex;
  gap: 8px;
  margin-bottom: 6px;
  align-items: center;
}
.ia-chat input[type="text"] {
  flex: 1;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid #d0d0d0;
  font-size: 15px;
  background: #fff;
  -webkit-appearance: none;
  appearance: none;
}
.ia-chat button {
  padding: 10px 12px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: #eaeaea;
  font-size: 15px;
  transition: background 120ms ease;
}
.ia-chat button:hover { background: #dedede; }

/* Footer controls */
.ia-chat .controls {
  display: flex;
  gap: 8px;
  align-items: center;
  margin-bottom: 6px;
}
.ia-chat .controls .rescan-btn {
  padding: 8px 10px;
  border-radius: 8px;
  border: 1px solid #ddd;
  background: #fff;
  cursor: pointer;
  font-size: 14px;
}
.ia-chat .controls .counter {
  color: #666;
  font-size: 13px;
  margin-left: auto;
}

/* Risposte visibili (lista) */
.ia-chat .results {
  margin-top: 8px;
  font-size: 13px;
}
.ia-chat .results ul {
  margin: 6px 0 0 18px;
  padding: 0;
}
.ia-chat .results li {
  margin-bottom: 6px;
  padding: 6px 4px;
  border-radius: 6px;
}

/* ==========================
Pulsante toggle (apri/chiudi)
========================== */
/* Pulsante flottante piccolo che rimane visibile quando la chat è nascosta */
.ia-toggle {
  position: fixed;
  right: 12px;
  bottom: 12px;
  z-index: 10000;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, #007aff, #0058d6);
  color: #fff;
  border: none;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  cursor: pointer;
  transition: transform 160ms ease, opacity 140ms ease;
  font-size: 22px;
}

/* Quando la chat è visibile, nascondiamo il toggle (per evitare duplicati) */
.ia-chat-visible + .ia-toggle,
.ia-chat:not(.hidden) + .ia-toggle {
  opacity: 0;
  pointer-events: none;
  transform: scale(0.85);
}

/* Stato "minimizzato" (toggle visibile) per accessibilità */
.ia-toggle[aria-expanded="false"] {
  opacity: 1;
  transform: scale(1);
}

/* Icone semplici (puoi sostituire con SVG) */
.ia-toggle .icon {
  display: inline-block;
  line-height: 1;
}

/* ==========================
Accessibilità & focus
========================== */
.ia-chat button:focus,
.ia-chat input:focus,
.ia-toggle:focus {
  outline: 3px solid rgba(52,152,219,0.18);
  outline-offset: 2px;
  border-radius: 10px;
}

/* Improve safe-area support (iPhone notch) */
:root {
  --safe-bottom: env(safe-area-inset-bottom, 12px);
}
.ia-chat { bottom: calc(12px + var(--safe-bottom)); right: 12px; left: 12px; }
.ia-toggle { bottom: calc(12px + var(--safe-bottom)); }

/* Prevent body scroll jump when chat input focused on mobile (soft keyboard) */
html, body {
  -webkit-text-size-adjust: 100%;
  -webkit-user-select: text;
  touch-action: manipulation;
}

/* ==========================
Transizioni e performance
========================== */
/* Smooth scrolling within chat window already enabled above */

/* Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .ia-chat, .ia-toggle {
    transition: none;
  }
}

/* ==========================
Responsive tweaks (tablet and desktop)
========================== */
@media (min-width: 641px) {
  .ia-chat {
    width: 380px;
    max-width: calc(100% - 32px);
    left: auto;
    right: 16px;
    bottom: 16px;
    padding: 14px;
    border-radius: 12px;
  }
  .ia-chat .chat-window { max-height: 180px; min-height: auto; }
  .articolo { width: calc(50% - 10px); }
  nav ul { flex-direction: row; gap: 12px; align-items: center; }
}

/* ==========================
Responsive tweaks original fallbacks
========================== */
@media (max-width: 900px) {
  .articolo { width: calc(50% - 10px); }
}
@media (max-width: 640px) {
  .articolo { width: 100%; padding: 16px; }
  nav ul { flex-direction: column; align-items: flex-start; gap: 6px; }
  header { border-radius: 0; }
}

/* ==========================
Extra: utilità per stato nascosto
========================== */
/* Utility class da applicare all'elemento body quando si vuole forzare la chat nascosta */
.chat-hidden .ia-chat { transform: translateY(120%); opacity: 0; visibility: hidden; pointer-events: none; }
